import "./style.css";
import logo from "./img/logo.svg";
import { HashLink as Link } from "react-router-hash-link";
import ic1 from "./img/ic1.svg";
import ic2 from "./img/ic2.svg";
import ic3 from "./img/ic3.svg";
import circles from "./img/circles.svg";
import res from "./img/fres.png";
import soon from "./img/soon.png";
import phone from "./img/phone.png";
import tab from "./img/plan.png";
import tg from "./img/tg.svg";
import inst from "./img/inst.svg";
import strel from "./img/strel.svg";
import mobtab from "./img/tab.png";
import stars from "./img/stars.svg";
import mobtr from "./img/mobtr.svg";
import { Fade, Flip, Rotate, Zoom } from "react-reveal";
export default function Header() {
  return (
    <div className="Header">
      <div className="headerTop">
        <img src={logo} alt="" className="logo" />
        <div className="mobileButton">
          <div></div>
          <div></div>
          <div></div>
        </div>
        <ul>
          <li>
            <Link to={"#tera"}>Calculator</Link>
          </li>
          <li>
            <Link to={"#work"}>How it works</Link>
          </li>
          <li>
            <Link to={"#steps"}>Step by step</Link>
          </li>
        </ul>
        <div
          className="signUp"
          onClick={() =>
            window.open("https://my.everminer.io/registration", "_blank")
          }
        >
          Sign up
        </div>
        <div
          className="login"
          onClick={() => window.open("https://my.everminer.io/", "_blank")}
        >
          Login
        </div>
      </div>
      <h1>
        <div className="etern">Eternal </div>
        bitcoin {document.body.clientWidth < 640 && <br />} mining
      </h1>
      <Fade left duration={700}>
        <img src={phone} className="phone" alt="" />
      </Fade>{" "}
      <Fade right duration={700}>
        <img
          src={document.body.clientWidth > 640 ? tab : mobtab}
          className="tab"
          alt=""
        />{" "}
      </Fade>
      <div className="cir">
        <div class="ripple-background">
          <div class="circle xxlarge shade1"></div>
          <div class="circle xlarge shade2"></div>
          <div class="circle large shade3"></div>
          <div class="circle mediun shade4"></div>
          <div class="circle small shade5"></div>
        </div>
      </div>
      <p className="all">All inclusive🔥</p>
      <div className="hots">
        <div className="hotEm">
          <img src={ic1} alt="" />
          <p>service</p>
        </div>
        <div className="hotEm">
          <img src={ic2} alt="" />
          <p>hosting</p>
        </div>
        <div className="hotEm">
          <img src={ic3} alt="" />
          <p>electricity</p>
        </div>
      </div>
      <div
        className="open"
        onClick={() =>
          window.open("https://my.everminer.io/registration", "_blank")
        }
      >
        Open account <img src={strel} alt="" />
      </div>
      <p className="tech"> See the technology in 5 minutes</p>
      <img src={stars} className="star" alt="" />
      <h2>
        It’s all <br /> simple
      </h2>
      <p className="depend">
        Depending on the <br /> amount, your monthly <br /> income grows
      </p>
      <div className="flexImg">
        <Zoom delay={500}>
          <img
            src={document.body.clientWidth > 640 ? res : mobtr}
            className="res"
            alt=""
          />
        </Zoom>
        <Rotate delay={500} right bottom>
          <img src={circles} className="circles" alt="" />
        </Rotate>
      </div>
    </div>
  );
}
